/* =====================================================

set Print

===================================================== */
@media print {
  section {
    display: block; }
  pre code {
    page-break-after: auto; }
  @page {
    margin-top: .4cm;
    margin: .5cm; }
  p a, code, pre {
    word-wrap: break-word; }
  pre a[href]:after {
    content: ""; }
  pre abbr[title]:after {
    content: ""; }
  pre.ir a:after,
  pre a[href^="javascript:"]:after,
  pre a[href^="#"]:after {
    content: ""; }
  .no_print {
    display: none; } }
